.blockquoteContainer{display:grid;row-gap:20px;margin-top:0}.blockquote{margin:0;font-size:1.5rem;quotes:"«" "»" "„" "“";position:relative}.blockquote::before,.blockquote::after{font-size:2.2rem}.blockquote::before{content:"„";position:absolute;left:-25px;line-height:.5}.blockquote::after{content:"“";position:relative;top:7px;left:3px;line-height:0}.blockquoteFooter{display:grid;row-gap:3px}.blockquoteFooter .authorAndSource .source{font-style:normal}.blockquoteId{margin-top:20px;font-size:.7rem;color:gray}